import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'

import 'animate.css';
// import { Col, Container, Row } from 'retrap';
export class Gym extends Component {
    render() {
        return (
            <CartProvider>
                {/* Start gym section  */}
                <hr className='new2'></hr>
                <h2 className="header-two animate__animated animate__backInLeft animate__delay-2s animate__repeat-2 " style={{ color: "white" }}>INFORMATION </h2>
                <hr className='new2'></hr>
                <div className="media border p-3">
                    <img src="/img/pexels-photo-4761351.webp" alt="John Doe" className="mr-3 mt-3 rounded-circle" style={{ width: "60px" }} />
                    <div className="media-bady">
                        <h4>
                            PERSONAL TRAINING<small><i>Cautch : Mick </i></small></h4>
                        <p className="wpw">We believe personal training is important in order for you to reach your fitness goals,<br /> whether it's for weight loss or sports driven.</p>
                        <div className="media p-3">
                            <img src="/img/pexels-photo-3768913.webp" alt="Jane Doe" className="mr-3 mt-3 rounded-circle" style={{ width: "90px", height: "60px" }} />
                            <div className="media-bady">
                                <h4>SPA <small><i>Cautch : lyly </i></small></h4>
                                <p className="wpw">Our Ambinence at the spa is too tempting to walk away from. after a goog workout, <br />pamper yourself in our jacuzzi, sauna, and steam room to feel even better!<br /> To top it off, our message sessions are to die for!</p>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End gym section  */}
            </CartProvider>

        )
    }
}



export default Gym;       