import { CartProvider } from "react-use-cart";
import React, { Component } from 'react';
import 'animate.css';
import { Col, Container, Row } from "reactstrap";

export class Fav extends Component {
    render() {
        return (
            <CartProvider>
                {/* Start fav section  */}
                <Container>
                    <Row>
                        <Col md="12">
                            <div id="accordion" className="mt-5 mb-5">
                                <div className="card" style={{ backgroundColor: "black" }}>
                                    <div className="card-header" id="headingOne">
                                        <h5 className="mb-0">
                                            <button className="btn btn-link" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                                MEN CLASSES
                                            </button>
                                        </h5>
                                    </div>

                                    <div id="collapseOne" className="collapse show" aria-labelledby="headingOne" data-parent="#accordion">
                                        <div className="card-body">
                                            It's not only the shape of your body that's important; your health is, too. Our variety of exercise classes allows your body to reflect on your health on the inside and out. The energy in the classes is the perfect way to stay motivated to accomplish your health goals in a fun way!
                                            <img src="/img/pexels-photo-896058.webp" alt="Mountains" style={{ width: "100%", height: "350px" }} />
                                        </div>
                                    </div>
                                </div>
                                <div className="card" style={{ backgroundColor: "black" }} >
                                    <div class="card-header" id="headingTwo">
                                        <h5 className="mb-0">
                                            <button className="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                                                LADIES CLASSES

                                            </button>
                                        </h5>
                                    </div>
                                    <div id="collapseTwo" className="collapse" aria-labelledby="headingTwo" data-parent="#accordion">
                                        <div className="card-body">
                                            Stay healthy from the inside out. Let nothing stop you even if it's pregnancy or recently having a baby. Our classes are full of variety and energy! The motivation is always alive! Get in shape and become healthier the fun way!
                                            <img src="/img/pexels-photo-28054.webp" alt="Mountains" style={{ width: "100%", height: "350px" }} />
                                        </div>
                                    </div>
                                </div>
                                <div className="card" style={{ backgroundColor: "black" }}>
                                    <div className="card-header" id="headingThree">
                                        <h5 className="mb-0">
                                            <button className="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                                                KIDS CORNER
                                            </button>
                                        </h5>
                                    </div>
                                    <div id="collapseThree" className="collapse" aria-labelledby="headingThree" data-parent="#accordion">
                                        <div className="card-body">
                                            We've got your preteens and teens (age 10-15)on the right track to staying healthy and fit. Their age is the most important timing for your children to be fit.
                                            <img src="/img/pexels-photo-296302.jpeg" alt="Mountains" style={{ width: "100%", height: "350px" }} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>
                {/* End fav section  */}
            </CartProvider >
        )
    }
}



export default Fav; 