import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'
import { Col, Container, Row } from "reactstrap";
import 'animate.css';
// import { Col, Container, Row } from 'retrap';
export class Leson extends Component {
    render() {
        return (
            <CartProvider>
                {/* Start leson section  */}
                <div>
                    <Container>
                        <Row>
                            <Col md="6">
                                <div className="card bg-dark  cf mt-5 text-center ">
                                    <img src="/img/pexels-photo-3184183.jpeg" className="card-img gg" alt="pic" />
                                    <div className="card-img-overlay ddd" >
                                        <h5 className="card-title">Enjoy healthy eating</h5>
                                        <p className="card-text">The Healthy Eating Plate encourages consumers to use healthy oils and <br />does not set a maximum daily calorie intake from healthy fat sources.</p>
                                        <p className="card-text">Last updated 5 mins ago</p>
                                    </div>
                                </div>
                            </Col>
                            <Col md="6">
                                <div className="card bg-dark cf mt-5 text-center">
                                    <img src="/img/pexels-photo-1247677.webp" className="card-img gg" alt="pic" />
                                    <div className="card-img-overlay ddd" >
                                        <h5 className="card-title">Healthy Eating Plate</h5>
                                        <p className="card-text">The Healthy Eating Plate encourages consumers to use healthy oils and <br />does not set a maximum daily calorie intake from healthy fat sources.</p>
                                        <p className="card-text">Last updated 3 mins ago</p>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
                {/* End leson section  */}
            </CartProvider >
        )
    }
}



export default Leson;           