import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'

// import { Col, Container, Row } from 'retrap';
export class Learn extends Component {
    render() {
        return (
            <CartProvider>
                <div>
                    {/* =========================
                    START  FLIP-CARD section
                    ========================= */}
                    <div className="mt-5 mb-5">
                        <hr className="new2"></hr>
                        <h2 className="text-center animate__animated animate__bounce hhhhh" style={{ color: "green" }}  > Look For About Our Booking</h2>
                        <hr className='new2'></hr>
                        <div className="container">

                            <div className="row">
                                <div className="col-md-4">
                                    <div className="flip-card mt-5">
                                        <div className="flip-card-inner">
                                            <div className="flip-card-front">
                                                <img src="/img/pexels-photo-3757375.webp"
                                                    alt="Avatat" style={{ width: "100%", height: "100%" }} />
                                            </div>
                                            <div className="flip-card-back">
                                                <h1 className="mt-5">For Long-Tearm <br></br>Reservation</h1>
                                                <p>30 days </p>
                                                <p>500$</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="flip-card mt-5">
                                        <div className="flip-card-inner">
                                            <div className="flip-card-front">
                                                <img src="/img/pexels-photo-3757954.webp"
                                                    alt="Avatat" style={{ width: "100%", height: "100%" }} />
                                            </div>
                                            <div className="flip-card-back">
                                                <h1 className="mt-5">For a Medium Term <br></br>Reservation</h1>
                                                <p>15 days</p>
                                                <p>300$</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-4">
                                    <div className="flip-card mt-5">
                                        <div className="flip-card-inner">
                                            <div className="flip-card-front">
                                                <img src="/img/pexels-photo-903171.jpeg" alt="Avatat" style={{ width: "100%", height: "100%" }} />
                                            </div>
                                            <div className="flip-card-back">
                                                <h1 className="mt-5">For a Short Term<br></br>Reservation</h1>
                                                <p>6 days </p>
                                                <p>200$</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>

                    </div>

                    {/* =========================
                    END  FLIP-CARD section
                    ========================= */}
                </div>
            </CartProvider >
        )
    }
}



export default Learn;   