import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'
import 'animate.css';
import { Col, Container, Row } from "reactstrap";
// import { Col, Container, Row } from 'retrap';
export class Media extends Component {
    render() {
        return (
            <CartProvider>
                <div>
                    {/* start media section  */}
                    <Container>
                        <Row>
                            <Col>
                                <div className="media mt-5">
                                    <img src="/img/pexels-photo-905326.webp" className="mr-3 ig" alt="pic" />
                                    <div className="media-body">
                                        <h5 className="mt-0 qww" style={{ color: "red" }}>The main message of the healthy eating <br />plate is to focus on a<br /> quality diet.</h5>
                                        <p className="qww">Choose healthy vegetable oils such as olive, <br />canola, soybean, corn, sunflower, peanut, <br />etc. You should also avoid partially<br /> hydrogenated oils that contain<br /> unhealthy trans fats.</p>

                                        <div className="media mt-3">
                                            <p className="mr-3" href="#">
                                                <img src="/img/pexels-photo-3962291.jpeg" className="iiii" alt="pic" style={{ height: "100%" }} />
                                            </p>
                                            <div className="media-body">
                                                <h5 className="mt-0 qww" style={{ color: "red" }} >Terms and <br />conditions for<br /> using the healthy <br />eating plate</h5>
                                                <p className="ppc qww">The type of carbohydrate is<br /> more important than the<br /> amount of carbohydrate in<br /> the diet because<br /> some carbohydrate<br /> sources such as<br /> vegetables other<br /> than potatoes,<br /> fruits, whole grains,<br /> and legumes are healthier<br /> than others.

                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </Col>
                        </Row>
                    </Container>

                    {/* End media section  */}
                </div>
            </CartProvider >
        )
    }
}



export default Media;           