import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'

import 'animate.css';
// import { Col, Container, Row } from 'retrap';
export class More extends Component {
    render() {
        return (
            <CartProvider>
                {/* Start More section  */}

                <div className="card mt-5 more" style={{ width: "500px" }}>
                    <img className="card-img-top" src="/img/pexels-photo-4498606.jpeg" alt="Cardimage" />
                    <div className="card-img-overlay mmmm">

                        <h4 className="card-title">HEALTHY</h4>
                        <p className="card-text">Get better and faster results with our personal trainers.<br />
                            Reduce muscle tension and pain at our massage sessions.<br />
                            On top of all that, our classes will make you reach your<br /> fitness goal in no time
                            with the energy that fills the room that keeps <br />you motivated at all times.</p>
                    </div>
                </div>
                <hr className='new2 mt-5'></hr>
                <h2 className="header-two animate__animated animate__backInLeft animate__delay-2s animate__repeat-2 " style={{ color: "white" }}>Take A Look</h2>
                <hr className='new2'></hr>
                <iframe className="vedio" width="990px" height="500px" src="https://www.youtube.com/embed/FBUmnC13Clg" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>

                {/* End More section  */}
            </CartProvider>

        )
    }
}



export default More; 