import React from "react";
import { NavLink } from "react-router-dom";
import { Link } from "react-scroll";
import { CartProvider, useCart } from "react-use-cart";
function Navbar() {
    const { totalItems } = useCart();
    return (
        <CartProvider>
            {/* start Navbar section  */}
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <NavLink className="navbar-brand ml-3" style={{ color: "red" }} to="/">WELCOME TO OUR GYM <i class="fa-solid fa-dumbbell" style={{ color: "red" }}></i></NavLink>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse " id="navbarSupportedContent">

                    <form className="form-inline my-1 my-lg-0  ml-3">
                        <ul className="navbar-nav  ">
                            <li className="nav-item active">
                                <NavLink className="nav-link " style={{ color: "blue" }} to="/Features">Features <span className="sr-only">(current)</span></NavLink>
                            </li>

                            <li className="nav-item">
                                <NavLink className="nav-link " style={{ color: "blue" }} to="/Download">Signup</NavLink>
                            </li>

                            <li className="nav-item">
                                <NavLink className="nav-link " style={{ color: "blue" }} to="/History">History</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" style={{ color: "blue" }} to="/About">AboutUs</NavLink>
                            </li>

                            <li className="nav-item">
                                <NavLink className="nav-link" style={{ color: "blue" }} to="/Collect">classes</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" style={{ color: "blue" }} to="/Product">Products</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" style={{ color: "blue" }} to="/Table"><i class="fa-solid fa-cart-shopping"></i>({totalItems})</NavLink>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="great" style={{ color: "blue" }} smooth={true} offset={-100} duration={1000}>Smooth</Link>
                            </li>

                        </ul>


                    </form>
                </div>
            </nav>
            {/* end Navbar section  */}
        </CartProvider>
    )
}
export default Navbar;