import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'
import { NavLink } from "react-router-dom";

export class Form extends Component {
    render() {
        return (
            <CartProvider>
                {/* start formm section  */}
                <div>
                    <div className="media mt-5 mb-5">
                        <div className="container mt-5">
                            <div className="row">
                                <div className="col-md-6 ">

                                    <img src="/img/pexels-photo-3768913.webp" className="img-fluid" style={{ height: '550px', borderradius: '1%;' }} alt="img" />

                                </div>
                                <div className="col-md-6 ">
                                    <h2 style={{ color: 'blue' }}>Creat Account</h2>
                                    <h6 className="text-muted my-4">Go ahead and sign up , let everyone <br />know how awesome you are!</h6>
                                    {/* <form onsubmit=" return validate();" action="index-3.html"> */}
                                    <form name="myForm" action="/Aboutpage.js" onsubmit="return validateForm()" method="post" required>
                                        <div id="error"></div>
                                        <div className="input-container">
                                            <i className="fa fa-user icon"></i>
                                            <input type="text" className="form-control mb-3 p-4 input-field " id="name"
                                                placeholder="username" name="fname" />
                                            <i className="fa fa-envelope icon"></i>
                                            <input type="text" className="form-control mb-3 p-4 input-field " id="email"
                                                placeholder="E-mail" />
                                            <i className="fa fa-key icon"></i>
                                            <input type="password" class="form-control mb-3 p-4 input-field  " id="password"
                                                placeholder="password" />
                                            <i className="fa fa-key icon"></i>
                                            <input type="password" className="form-control mb-3 p-4 input-field " id="confirmPass"
                                                placeholder="ConfirmPassword" />

                                            < NavLink className="nav-link btn btn-primary" to="/About">Submit</NavLink>
                                        </div>
                                    </form>
                                </div>

                            </div>

                        </div>
                    </div>
                </div>
                {/* End formm section  */}
            </CartProvider >
        )
    }
}



export default Form;           