import { CartProvider } from "react-use-cart";
import React, { Component } from 'react';
import 'animate.css';
import { Col, Container, Row } from "reactstrap";

export class Dina extends Component {
    render() {
        return (
            <CartProvider>
                {/* Start dina section  */}
                <div className="dinaa">
                    <h2 id="hhh">KEEP YOUR BODY</h2>
                    <div className="ddnn">

                        <p>Get better and faster results with our personal trainers.<br />
                            Reduce muscle tension and pain at our massage sessions.<br />
                            On top of all that, our classes will make you reach your fitness goal<br /> in no time
                            with the energy that fills the room that keeps <br />you motivated at all times.</p>
                    </div>
                    <Container>
                        <Row>
                            <Col md="12">
                                <div className="card-deck">
                                    <div className="card ttt">
                                        <img src="/img/istockphoto-1010878506-612x612.jpg" className="card-img-top dna" alt="pic" style={{ height: "250px" }} />
                                        <div className="card-body" style={{ color: "White", backgroundColor: "black" }}>
                                            <h5 className="card-title" style={{ color: "red" }}>WORKING HOURS</h5>
                                            <p className="card-text">MEN BRANCHES ARE AVAILABLE FROM 6 AM / 1 AM<br />
                                                LADIES BRANCHES ARE AVAILABLE FROM 7 AM / 11 PM</p>
                                            <p className="card-text"><small className="text-muted">WE ARE WAITING FOR YOU</small></p>
                                        </div>
                                    </div>
                                    <div className="card ttt">
                                        <img src="/img/istockphoto-1053276266-612x612.jpg" className="card-img-top dna" alt="pic" style={{ height: "250px" }} />
                                        <div className="card-body" style={{ color: "white", backgroundColor: "black" }} >
                                            <h5 className="card-title " style={{ color: " red" }} >LATEST NEWS</h5>
                                            <p className="card-text">VISIT OUR NEW GOLF LADIES BRANCH!<br />
                                                FIND US ON GOOGLE MAPS</p>

                                        </div>
                                    </div>
                                    <div className="card ttt ghgh">
                                        <img src="/img/pexels-photo-221247.webp" className="card-img-top dna" alt="pic" style={{ height: "250px" }} />
                                        <div className="card-body" style={{ color: "White", backgroundColor: "black" }} >
                                            <h5 className="card-title" style={{ color: "red" }} >FIND US</h5>
                                            <p className="card-text"><i class="fa-solid fa-envelope-open-text mr-2"></i>@dinagym.gmail.com <br /><i class="fa-solid fa-phone mr-2"></i>phone:+23456787980</p>

                                        </div>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </Container>

                </div>
                {/* End dina section  */}
            </CartProvider>


        )
    }
}



export default Dina;    