import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'
import 'animate.css';
import { Container, Row, Col } from "reactstrap";
// import { Col, Container, Row } from 'retrap';
export class Social extends Component {
    render() {
        return (
            <CartProvider>
                <div>
                    {/* Start social section  */}
                    <div>
                        <hr className='new2'></hr>
                        <h2 className="hh  sss animate__animated animate__backInLeft animate__delay-2s animate__repeat-3">
                            Information for a healthy eating system</h2>
                        <hr className='new2'></hr>
                    </div>
                    <Container>
                        <Row>

                            <div className="row row-cols-1 row-cols-md-2 mt-5">
                                <Col md="6">
                                    <div className="col mb-4">
                                        <div className="card cd ">
                                            <img src="/img/istockphoto-507213977-612x612.jpg" className="card-img-top animate__animated animate__backInLeft animate__delay-2s animate__repeat-3" alt="pic" />
                                            <div className="card-body">
                                                <h5 className="card-title">Most meals should contain vegetables and fruits</h5>
                                                <p className="card-text">This is in order to eat different types of vegetables and fruits of different colors,<br /> and remember that potatoes are not considered<br></br> among the vegetables that are eaten in a healthy eating dish.</p>
                                            </div>
                                        </div>
                                    </div>
                                </Col>
                                <Col md="6">
                                    <div className="col mb-4 ">
                                        <div className="card cd">
                                            <img src="/img/istockphoto-1208790371-612x612.jpg" class="card-img-top animate__animated animate__backInRight animate__delay-2s animate__repeat-3" alt="pic" />
                                            <div className="card-body">
                                                <h5 className="card-title">Eat whole grains</h5>
                                                <p className="card-text">
                                                    Whole and whole grains Whole wheat, barley, quinoa, oats, <br />brown rice and grain-added foods such as whole-wheat pasta<br /> have a milder effect on blood sugar and insulin than white bread.</p>
                                            </div>
                                        </div>
                                    </div>
                                </Col>
                                <Col md="6">
                                    <div className="col mb-4">
                                        <div className="card cd ">
                                            <img src="/img/pexels-photo-1099680.jpeg" className="card-img-top animate__animated animate__backInLeft animate__delay-2s animate__repeat-3 " alt="pic" />
                                            <div className="card-body">
                                                <h5 className="card-title">Energy protein</h5>
                                                <p className="card-text">Fish, poultry, legumes, and nuts are <br />all healthy and versatile sources of protein <br />and can be mixed with different salads or eaten with vegetables.<br /> Limit your intake of red meat and avoid processed meat.</p>
                                            </div>
                                        </div>
                                    </div>
                                </Col>
                                <Col md="6">
                                    <div className="col mb-4 ">
                                        <div className="card cd">
                                            <img src="/img/pexels-photo-892649.jpeg" className="card-img-top animate__animated animate__backInRight animate__delay-2s animate__repeat-3" alt="pic" />
                                            <div className="card-body">
                                                <h5 className="card-title">
                                                    Healthy vegetable oils</h5>
                                                <p className="card-text">Choose healthy vegetable oils such as olive, <br />canola, soybean, corn, sunflower, peanut, etc. You should also avoid partially<br /> hydrogenated oils that contain unhealthy trans fats.</p>
                                            </div>
                                        </div>
                                    </div>
                                </Col>
                            </div>

                        </Row>
                    </Container>

                    {/* End social section  */}
                </div>
            </CartProvider >
        )
    }
}



export default Social;      