import { CartProvider } from "react-use-cart";
import React, { Component } from 'react'
import { NavLink } from "react-router-dom";
// import { Col, Container, Row } from 'retrap';
export class Booking extends Component {
    render() {
        return (
            <CartProvider>
                <div>
                    {/* =======================         
                        START BOOKING SECTION 
                        ======================== */}

                    <div className="great mt-5" id="great">
                        <div className="container">
                            <div className="row">
                                <div className="booking ">
                                    <div className="card bg-dark text-dark">
                                        <img src="/img/pexels-photo-905336.jpeg"
                                            className="card-img" alt="pic8" />
                                        <div className="card-img-overlay">
                                            <div className="op">
                                                <h5 className="card-title text-dark ml-3 bb">START BOOKING NOW !</h5>
                                                <form>
                                                    <div className="form-group row">

                                                        <div className="col-sm-10">
                                                            <input type="text" className="form-control ml-3 fo"
                                                                id="inputEmail3 pp" placeholder="Full Name" />
                                                        </div>
                                                    </div>
                                                    <div className="form-group row">

                                                        <div className="col-sm-10">
                                                            <input type="text" className="form-control ml-3 fo" id="inputPassword3 pp"

                                                                placeholder="Email Address" />
                                                        </div>
                                                    </div>
                                                    <fieldset className="form-group row">

                                                        <div className="col-sm-10">
                                                            <div className="form-check">
                                                                <input className="form-check-input ml-4 " type="radio"
                                                                    name="gridRadios" id="gridRadios1 pp" value="option1" checked />
                                                                <label class="form-check-label ml-5 " id="pp" for="gridRadios1">
                                                                    Mix Groub
                                                                </label>
                                                            </div>
                                                            <div className="form-check">
                                                                <input className="form-check-input ml-4 " type="radio"
                                                                    name="gridRadios" id="gridRadios2 pp" value="option2" />
                                                                <label className="form-check-label ml-5" id="pp" for="gridRadios2">
                                                                    Men's Groub
                                                                </label>
                                                            </div>
                                                            <div className="form-check">
                                                                <input className="form-check-input ml-4 " type="radio"
                                                                    name="gridRadios" id="gridRadios3 pp" value="option3" />
                                                                <label className="form-check-label ml-5" id="pp" for="gridRadios3">
                                                                    Women's Groub
                                                                </label>
                                                            </div>

                                                        </div>
                                                    </fieldset>

                                                    <div className="form-group row">
                                                        <div className="col-sm-10">
                                                            <NavLink className=" nav-link btn btn-danger ml-3 bbbb" to="/Download">Next Step</NavLink>
                                                        </div>
                                                    </div>
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* =======================         
                        END BOOKING SECTION 
                       ======================== */}
                </div>
            </CartProvider >
        )
    }
}



export default Booking;    