import { CartProvider } from "react-use-cart";
import React, { Component } from 'react';
import 'animate.css';
import { Col, Container, Row } from "reactstrap";

export class Fom extends Component {
    render() {
        return (
            <CartProvider>
                {/* Start fom section  */}
                <Container>
                    <Row>
                        <Col md="4">
                            <div className="card border-primary mb-3" style={{ maxWidth: "18rem" }}>
                                <div className="card-header  border-primary" style={{ backgroundColor: "black" }}  >FAMILY FUN</div>
                                <div className="card-body text-primary" style={{ backgroundColor: "black" }}>
                                    <h5 className="card-title text-primary " style={{ backgroundColor: "black" }} >Fun : </h5>
                                    <p className="card-text text-primary " style={{ backgroundColor: "black" }}>We have something for your family to do when you're at our branches.</p>
                                </div>
                                <div className="card-footer border-primary " style={{ backgroundColor: "black" }}>Came with your family</div>
                            </div>
                        </Col>
                        <Col md="4">
                            <div className="card border-warning mb-3" style={{ maxWidth: "18rem" }}>
                                <div className="card-header  border-warning" style={{ backgroundColor: "black" }}  >TRUE EXPERTISE</div>
                                <div className="card-body text-warning" style={{ backgroundColor: "black" }}>
                                    <h5 className="card-title text-warning " style={{ backgroundColor: "black" }} >Expertise : </h5>
                                    <p className="card-text text-warning " style={{ backgroundColor: "black" }}>Our trainers are highly qualified and know what they're doing.It's beautiful.</p>
                                </div>
                                <div className="card-footer border-warning" style={{ backgroundColor: "black" }}>You will see</div>
                            </div>

                        </Col>
                        <Col md="4">
                            <div className="card border-success mb-3" style={{ maxWidth: "18rem" }}>
                                <div className="card-header  border-success" style={{ backgroundColor: "black" }}  >NEWEST OF THE NEW</div>
                                <div className="card-body text-success" style={{ backgroundColor: "black" }}>
                                    <h5 className="card-title text-success " style={{ backgroundColor: "black" }} >New : </h5>
                                    <p className="card-text text-success " style={{ backgroundColor: "black" }}>Our classes are always fresh and so is our equipment! You'll always stay excited and motivated.</p>
                                </div>
                                <div className="card-footer border-success " style={{ backgroundColor: "black" }}>You will enjouy</div>
                            </div>

                        </Col>
                    </Row>
                </Container>
                {/* End fom section  */}
            </CartProvider >
        )
    }
}



export default Fom; 