
import { CartProvider } from "react-use-cart";
import React, { Component } from 'react';
import { NavLink } from "react-router-dom";
import 'animate.css';
// import { Col, Container, Row } from 'retrap';
export class Fun extends Component {
    render() {
        return (
            <CartProvider>
                <div>
                    {/* ====================
                        START FUN SECTION 
                        =================== */}
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12">
                                <hr className='new2'></hr>
                                <h2 className="header-two animate__animated animate__backInLeft animate__delay-2s animate__repeat-3 " style={{ color: "green" }}>ABOUT OUR GYM</h2>
                                <hr className='new2'></hr>
                            </div>
                            <div className="col-md-6 mt-5">
                                <h3 className="media">BODY TRANSFORMATION</h3>
                                <p className="text-muted">Helping you achieve your weight loss and muscle goals.</p>
                                <h3 className="media">FAMILY FUN</h3>
                                <p className="text-muted">We have something for your family to do when you're at our branches.</p>
                                <h3 className="media">FLEXIBLE MEMBERSHIPS</h3>
                                <p className="text-muted">Offering you various packages that suit you best.</p>
                                <h3 className="media">ALWAYS INNOVATIVE</h3>
                                <p className="text-muted">Providing you with the top-notch classes and newest equipment.</p>

                                <NavLink className="btn btn-danger nav-link" to="/History">Learn More</NavLink>
                            </div>
                            <div className="col-md-6 mt-5">
                                <div className="column">
                                    <img src="/img/pexels-photo-3912953.webp"
                                        className=" yyy  img-media animate__animated animate__backInLeft animate__delay-2s animate__repeat-3" onclick="myFunction(this);" alt="pic2" />
                                </div>
                                <div className="column">
                                    <img src="/img/pexels-photo-116079.jpeg"
                                        class=" yyy img-media mt-5" onclick="myFunction(this);" alt="pic3" />
                                </div>
                                <div className="column">
                                    <img src="/img/pexels-photo-3912366.webp "
                                        className="  yyy img-media mt-2 animate__animated animate__backInRight animate__delay-2s animate__repeat-3" onclick="myFunction(this);" alt="pic4" />
                                </div>
                            </div>

                        </div>
                    </div>

                    {/* ====================
END FUN SECTION 
=================== */}



                </div>

            </CartProvider >
        )
    }
}



export default Fun;   